#root {
    height: 100%;
    overflow: hidden;
}
.Board {
    width: calc(100vh - 138px);
    min-width: 800px;
    height: calc(100vh - 138px);
    min-height: 800px;
    margin: auto;
    margin-top: 10px;
}

.Card {
    float: left;
    width: 25%;
    height: 25%;
    padding: 0;
    margin: 0;
    border: 10px solid #f7f7f7;
    text-align: center;
    background-color: white;
    background:  url('images/back.png');
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    position: relative;
}


.Card .team {
    opacity: 0;
    height: 100%;
}

.Card--flipped {
    background-color: gray;
}

.Card--flipped .team {
    opacity: 1;
    -webkit-transition: opacity 0.3s;
    -o-transition: opacity 0.3s;
    transition: opacity 0.3s;
}
.Card--matched .team {
    opacity: 1;
    -webkit-transition: opacity 0.3s;
    -o-transition: opacity 0.3s;
    transition: opacity 0.3s;
}

.overlay-matched {
    height: 100%;
    width: 100%;
    background: #2fc79e;
    z-index: 100;
    position: absolute;
    top: 0;
    display: block;
    opacity: 0;
    -webkit-transition: opacity 0.2s;
    -o-transition: opacity 0.2s;
    transition: opacity 0.2s;
}

.Card--matched {
    background-color: green;
}
.Card--matched .overlay-matched {
    opacity: 0.5;
}
#ResetButton, #PlayAgain, #SeeStats {
    position: absolute;
    right: 15px;
    top: 15px;
    z-index: 10;
    min-width: 150px;
}
 #SeeStats {
    top: 80px;
 }
.legend{
    font-size: 16px;
    margin-right: -5px;
}
#BtnWinner,
#BtnReset {
    margin-top: 15px;
    -webkit-transition: none;
    -o-transition: none;
    transition: none;
    border: none;
}
 #BtnReset {
    background: white;
    color: #00a8ff;
}
#BtnWinner:hover,
#BtnWinner:focus,
#BtnWinner:active {
    background: none;
    border: none;
 }
.loader {
    height: calc(100vh - 20px);
    text-align: center;
    padding-top: 8vw;
}
#Winner {
    position: absolute;
    top: 200px;
    top: calc(50vh - 80px);
    padding: 100px 0 80px;
    width: 100%;
    z-index: 9;
}

#Timer, #TimerBest {
    position: absolute;
    left: 21%;
    top: 10px;
}
#TimerBest {
    top: 40px;
    color: #75d8bd;
}
#Stats {
    background: #f7f7f7;
    min-height: 100vh;
    padding-bottom: 50px;
    padding-top: 10px;
}
#statsCenter {
    width: calc(100vh - 138px);
    min-width: 800px;
    margin: auto;
}
#userWell {
    background: white;
    position: fixed;
    left: 20px;
    top:20px;
    text-align: center;
    z-index: 1;
    border: none;
    padding: 15px;
    padding-bottom: 0;
    border-radius: 0;
}

#userWell h5 {
    margin: 0;
}
#userWell p {
    line-height: 18px;
    font-size: 16px;
}
#userWell img {
    width: 180px;
}
.reset {
    color: black;
    padding: 5px;
    font-size: 14px;
    outline: 0;
}
.reset:hover,
.reset:focus,
.reset:active {
    color: black;
    outline: 0;
}




