.white {
  color: white;
}

.light-white {
  color: rgba(255, 255, 255, 0.5);
}

.white-bg {
  background: white;
}

.gray-bg {
  background: #f7f7f7;
}

.blue {
  color: #00a8ff;
}

.blue-bg {
  background: #00a8ff;
}

.muted {
  color: #989da0;
}

.margin-top {
  margin-top: 150px;
}

@media(max-width:992px) {
  h1 {
    font-size: 36px;
  }
  h2 {
    font-size: 28px;
  }
  h3 {
    font-size: 24px;
  }
  h4 {
    font-size: 20px;
  }
  h5 {
    font-size: 16px;
  }
  h6 {
    font-size: 12px;
  }
  .section {
    padding: 30px 0;
  }
}

/* Media Queries */

@media(max-width:991px) {
  .text-center-mobile {
    text-align: center !important;
  }
}

@media(max-width: 768px) {
  .pricing {
    margin-bottom: 30px;
  }
  .pricings .pricing .info-icon {
    display: none;
  }
  .pricings .pricing .box-main,
  .pricings .pricing .box-second {
    left: 0;
    width: 100%;
    padding: 50px 50px 0;
    text-align: left;
    background: #00a8ff;
  }
  .pricings .pricing .box-main.active {
    background: #00a8ff;
    -webkit-transform: translateX(0%);
    -ms-transform: translateX(0%);
    transform: translateX(0%);
  }
  .pricings .pricing .box-second {
    position: relative;
    opacity: 1;
  }
  .popup-form {
    width: 100%;
    margin: 60px auto;
  }
  .modal {
    padding: 0 10px !important;
  }
  .popup-form .form-control:not(.dropdown):focus {
    position: relative;
    padding-right: 30px;
    padding-left: 30px;
  }
}

@media(max-width: 400px) {
  header .typed-cursor {
    display: none;
  }
  .pricings .pricing .box-second {
    padding-top: 0;
    font-size: 75%;
  }
}







